import { setAttribute, setComponentProps } from './index'

export function diff (dom, vnode, container) {
  const ret = diffNode(dom, vnode)

  if (container) {
    container.appendChild(ret)
  }

  return ret
}

// ÂØπÊØî
function diffNode (dom, vnode) {
  let out = dom;
  if (!vnode || typeof vnode === 'boolean') return;

  // Â¶ÇÊûúÊòØnumber ËΩ¨ÊàêÂ≠óÁ¨¶‰∏≤
  if (typeof vnode === 'number') {
    vnode = String(vnode)
  }
  // Â¶ÇÊûú vnode ÊòØÂ≠óÁ¨¶‰∏≤
  if (typeof vnode === "string") {
    if (dom && dom.nodeType === 3) {
      if (dom.textContent !== vnode) {
        // Êõ¥Êñ∞ÊñáÊú¨ÂÜÖÂÆπ
        dom.textContent = vnode
      }
    } else {
      out = document.createTextNode(vnode)
      if (dom && dom.parentNode) {
        // ÊõøÊç¢ÊñáÊú¨
        dom.parentNode.replaceNode(out, dom)
      }
    }

    return out
  }

  if (typeof vnode.tag === 'function') {
    diffComponent(out, vnode)
  }

  // ÈùûÊñáÊú¨DomËäÇÁÇπ
  if (!dom) {
    // ÂàõÂª∫ËäÇÁÇπÂØπË±°
    out = document.createElement(vnode.tag);
  }

  // ÊØîËæÉÂ≠êËäÇÁÇπ
  if (vnode.childrens && vnode.childrens.length > 0 || (out.childNodes && out.childNodes.length > 0)) {
    diffChildren(out, vnode.childrens)
  }

  diffAttribute(out, vnode)

  return out
}

function unmountComonent (comp) {
  removeNode(comp.base)
}
function removeNode (dom) {
  if (dom && dom.parentNode) {
    dom.parentNode.removeNode(dom);
  }
}

function diffChildren (dom, vnodes) {
  const domChildren = dom.childNodes
  const children = []
  const keyd = {}

  // Â∞ÜÊúâkeyÁöÑËäÇÁÇπ(Áî®ÂØπË±°‰øùÂ≠ò) Ê≤°ÊúâkeyÁöÑËäÇÁÇπ(Áî®Êï∞ÁªÑ‰øùÂ≠ò) ÂàÜÂºÄ
  if (domChildren.length > 0) {
    [...domChildren].forEach(item => {
      const key = item.key
      if (key) {
        keyd[key] = item
      } else {
        children.push(item)
      }
    })
  }

  if (vnodes && vnodes.length > 0) {
    let min = 0
    let childrenLen = children.length;
    [...vnodes].forEach((vnode, i) => {
      // Ëé∑ÂèñËôöÊãüdomÊâÄÊúâÁöÑkey
      const key = vnode.key
      let child;
      if (key) {
        // Â¶ÇÊûúÊúâkey ÊâæÂà∞ÂØπÂ∫îkeyÂÄºÁöÑËäÇÁÇπ
        if (keyd[key]) {
          child = keyd[key]
          keyd[key] = undefined
        }
      } else if (childrenLen > min) {
        // Â¶ÇÊûúÊ≤°Êúâkey Âàô‰ºòÂÖàÊâæÁ±ªÂûãÁõ∏ÂêåÁöÑËäÇÁÇπ
        for (let j = 0; j < children.length; j++) {
          let c = array[j];
          if (c) {
            child = c
            children[j] = undefined
            if (j === childrenLen - 1) childrenLen--
            if (j === min) min++
            break
          }
        }
      }

      // ÂØπÊØî
      child = diffNode(child, vnode)
      // Êõ¥Êñ∞dom
      const f = domChildren[i]

      if (child && child !== dom && child !== f) {
        // Â¶ÇÊûúÊõ¥Êñ∞ÂâçÂØπÂ∫î‰ΩçÁΩÆÁöÑËäÇÁÇπ‰∏∫Á©∫, Ë°®Á§∫Êñ∞Â¢û
        if (!f) {
          dom.appendChild(child)
        } else if (child === f.nextSibling) { // Â¶ÇÊûúÊõ¥Êñ∞ÂêéÁöÑËäÇÁÇπÂíåÊõ¥Êñ∞ÂâçÁöÑËäÇÁÇπ‰∏ã‰∏Ä‰∏™ËäÇÁÇπ‰∏ÄÊ†∑ ËØ¥ÊòéÂΩìÊúü‰ΩçÁΩÆÁöÑËäÇÁÇπË¢´Âà†Èô§‰∫Ü
          removeNode(f)
        } else { // Â∞ÜÊõ¥Êñ∞ÂêéÁöÑËäÇÁÇπÁßªÂä®Âà∞Ê≠£Á°ÆÁöÑ‰ΩçÁΩÆ
          dom.insertBefore(child, f) // Ê≥®ÊÑèinsertBeforeÁöÑÁî®Ê≥ïÔºåÁ¨¨‰∏Ä‰∏™ÂèÇÊï∞ÊòØË¶ÅÊèíÂÖ•ÁöÑËäÇÁÇπÔºåÁ¨¨‰∫å‰∏™ÂèÇÊï∞ÊòØÂ∑≤Â≠òÂú®ÁöÑËäÇÁÇπ
        }
      }
    })
  }
}

function diffAttribute (dom, vnode) {
  // ÁºìÂ≠ò‰πãÂâçÁöÑdomÁöÑÂ±ûÊÄß
  const oldAttrs = {}
  const newAttrs = vnode.attrs || {}
  const domArrts = document.querySelector('#root').attributes;
  [...domArrts].forEach(item => {
    oldAttrs[item.name] = item.value
  })

  // ÊØîËæÉÊñ∞ËÄÅÂ±ûÊÄß
  // Â¶ÇÊûúÂéüÊù•Â±ûÊÄß‰∏çÂú®Êñ∞Â±ûÊÄß‰∏≠ ÂàôÁßªÈô§
  for (const key in oldAttrs) {
    if (!(key in newAttrs)) {
      setAttribute(dom, key, undefined)
    }
  }

  // Êõ¥Êñ∞
  for (const key in newAttrs) {
    // ÂÄº‰∏çÂêå Êõ¥Êñ∞ÂÄº
    if (oldAttrs[key] !== newAttrs[key]) {
      setAttribute(dom, key, newAttrs[key])
    }
  }
}

function diffComponent (dom, vnode) {
  const comp = dom
  // Â¶ÇÊûúÁªÑ‰ª∂Ê≤°ÊúâÂèòÂåñ ÈáçÊñ∞ËÆæÁΩÆprops
  if (comp && comp.constructor === vnode.tag) {
    // ÈáçÊñ∞ËÆæÁΩÆprops
    setComponentProps(comp, vnode.attrs)
    // ËµãÂÄº
    dom = comp.base
  } else {
    // ÁªÑ‰ª∂ÂèëÁîüÂèòÂåñ
    if (comp) {
      unmountComonent(comp)
      comp = null
    }
  }
}
